val ProductDetailContainerStyle = CssStyle {
    base {
        Modifier
            .fillMaxWidth()
            .gap(2.cssRem)
            .display(org.jetbrains.compose.web.css.DisplayStyle.Grid)
            .gridTemplateColumns { size(1.fr) }
    }
    Breakpoint.MD {
        Modifier.gridTemplateColumns {
            size(40.percent)
            size(60.percent)
        }
    }
}

@InitRoute
fun initProductDetailPage(ctx: InitRouteContext) {
    ctx.data.add(PageLayoutData("Product Details"))
}

@Page("/products/{productId}")
@Layout(".components.layouts.PageLayout")
@Composable
fun ProductDetailPage() {
    val ctx = rememberPageContext()
    val productId = ctx.route.params["productId"]?.let {
        decodeURIComponent(it)
    } ?: ""

    val viewModel = remember { GlobalContext.get().get<ProductDetailViewModel>() }
    val state by viewModel.state.collectAsState()

    DisposableEffect(Unit) {
        onDispose {
            viewModel.onDispose()
        }
    }

    LaunchedEffect(productId) {
        viewModel.onEvent(ProductDetailUiEvent.LoadProduct(productId))
    }

    Column(
        modifier = Modifier.fillMaxWidth().gap(1.cssRem),
        horizontalAlignment = Alignment.Start
    ) {
        // Back Button
        Row(
            modifier = Modifier
                .cursor(Cursor.Pointer)
                .onClick { ctx.router.tryRoutingTo("/") }
                .gap(0.5.cssRem)
                .padding(0.5.cssRem),
            verticalAlignment = Alignment.CenterVertically
        ) {
            FaArrowLeft(
                modifier = Modifier
                    .fontSize(1.cssRem)
                    .color(ColorMode.current.toSitePalette().brand.primary)
            )
            SpanText(
                "Volver a Productos",
                Modifier
                    .fontSize(1.cssRem)
                    .color(ColorMode.current.toSitePalette().brand.primary)
            )
        }

        // Content based on state
        when (val currentState = state) {
            is ProductDetailUiState.Loading -> {
                SpanText(
                    "Loading...",
                    Modifier
                        .fontSize(2.cssRem)
                        .fontWeight(FontWeight.Bold)
                        .color(ColorMode.current.toSitePalette().brand.primary)
                )
            }
            is ProductDetailUiState.Success -> {
                ProductDetailContent(currentState.product)
            }
            is ProductDetailUiState.NotFound -> {
                Column(
                    modifier = Modifier.fillMaxWidth().gap(1.cssRem),
                    horizontalAlignment = Alignment.CenterHorizontally
                ) {
                    SpanText(
                        "Product Not Found",
                        Modifier
                            .fontSize(2.cssRem)
                            .fontWeight(FontWeight.Bold)
                            .color(ColorMode.current.toSitePalette().brand.primary)
                    )
                    SpanText(
                        "The product you're looking for doesn't exist.",
                        Modifier
                            .fontSize(1.cssRem)
                            .color(org.jetbrains.compose.web.css.rgba(100, 100, 100, 0.8))
                    )
                }
            }
        }
    }
}

@Composable
private fun ProductDetailContent(product: ProductItemElement) {
    Div(ProductDetailContainerStyle.toModifier().toAttrs()) {
        // Left Column - Image
        Column(
            modifier = Modifier.fillMaxWidth(),
            horizontalAlignment = Alignment.CenterHorizontally
        ) {
            Image(
                src = product.imageUrl,
                modifier = Modifier
                    .fillMaxWidth()
                    .height(400.px)
                    .borderRadius(1.cssRem)
                    .objectFit(ObjectFit.Cover)
                    .boxShadow(
                        offsetX = 0.px,
                        offsetY = 4.px,
                        blurRadius = 8.px,
                        color = org.jetbrains.compose.web.css.rgba(0, 0, 0, 0.15)
                    )
            )
        }

        // Right Column - Details
        Column(
            modifier = Modifier.fillMaxWidth().gap(1.5.cssRem),
            horizontalAlignment = Alignment.Start
        ) {
            // Product Name
            SpanText(
                product.name,
                Modifier
                    .fontSize(2.5.cssRem)
                    .fontWeight(FontWeight.Bold)
                    .color(ColorMode.current.toSitePalette().brand.primary)
            )

            // Sell Type Badge
            Box(
                modifier = Modifier
                    .padding(0.5.cssRem, 0.25.cssRem)
                    .borderRadius(0.5.cssRem)
                    .backgroundColor(ColorMode.current.toSitePalette().brand.accent)
            ) {
                SpanText(
                    getSellTypeLabel(product.sellType),
                    Modifier
                        .fontSize(0.9.cssRem)
                        .fontWeight(FontWeight.Bold)
                        .color(org.jetbrains.compose.web.css.rgb(255, 255, 255))
                )
            }

            // Price
            SpanText(
                "$${product.price}",
                Modifier
                    .fontSize(3.cssRem)
                    .fontWeight(FontWeight.Bold)
                    .color(ColorMode.current.toSitePalette().brand.accent)
            )

            // Description
            Column(
                modifier = Modifier.gap(0.5.cssRem)
            ) {
                SpanText(
                    "Description",
                    Modifier
                        .fontSize(1.3.cssRem)
                        .fontWeight(FontWeight.Bold)
                        .color(ColorMode.current.toSitePalette().brand.primary)
                )
                SpanText(
                    product.description,
                    Modifier
                        .fontSize(1.cssRem)
                        .lineHeight(1.6)
                        .color(org.jetbrains.compose.web.css.rgba(100, 100, 100, 0.9))
                )
            }

            // Categories
            if (product.categoryElement.isNotEmpty()) {
                Column(
                    modifier = Modifier.gap(0.5.cssRem)
                ) {
                    SpanText(
                        "Categories",
                        Modifier
                            .fontSize(1.3.cssRem)
                            .fontWeight(FontWeight.Bold)
                            .color(ColorMode.current.toSitePalette().brand.primary)
                    )
                    product.categoryElement.forEach { category ->
                        Column(
                            modifier = Modifier
                                .gap(0.25.cssRem)
                                .padding(top = 0.5.cssRem)
                        ) {
                            SpanText(
                                category.name,
                                Modifier
                                    .fontSize(1.1.cssRem)
                                    .fontWeight(FontWeight.SemiBold)
                                    .color(ColorMode.current.toSitePalette().brand.accent)
                            )
                            SpanText(
                                category.description,
                                Modifier
                                    .fontSize(0.95.cssRem)
                                    .lineHeight(1.5)
                                    .color(org.jetbrains.compose.web.css.rgba(100, 100, 100, 0.9))
                            )
                            if (category.benefits.isNotEmpty()) {
                                Column(
                                    modifier = Modifier.gap(0.15.cssRem).padding(left = 1.cssRem, top = 0.25.cssRem)
                                ) {
                                    category.benefits.forEach { benefit ->
                                        SpanText(
                                            " $benefit",
                                            Modifier
                                                .fontSize(0.9.cssRem)
                                                .color(org.jetbrains.compose.web.css.rgba(100, 100, 100, 0.8))
                                        )
                                    }
                                }
                            }
                        }
                    }
                }
            }
        }
    }
}

private fun getSellTypeLabel(sellType: SellType): String {
    return when (sellType) {
        SellType.Kilogram -> "per Kilogram"
        SellType.G100 -> "per 100g"
        SellType.Liter -> "per Liter"
        SellType.Unit -> "per Unit"
        SellType.Other -> "Other"
    }
}

// External function for URL decoding
external fun decodeURIComponent(encodedURI: String): String
