package mzx.ventas.di

import mzx.ventas.data.repository.ProductsRepository
import mzx.ventas.data.repository.ProductsRepositoryImp
import mzx.ventas.domain.usecase.GetProductByNameUseCase
import mzx.ventas.domain.usecase.GetProductByNameUseCaseImpl
import mzx.ventas.domain.usecase.GetProductsUseCase
import mzx.ventas.domain.usecase.GetProductsUseCaseImpl
import mzx.ventas.productdetail.ProductDetailViewModel
import mzx.ventas.products.ProductsViewModel
import mzx.ventas.service.LocalReaderService
import mzx.ventas.service.LocalReaderServiceImp
import org.koin.core.module.dsl.factoryOf
import org.koin.core.module.dsl.singleOf
import org.koin.dsl.bind
