val appModule = module {

    factoryOf(::LocalReaderServiceImp) bind LocalReaderService::class

    factoryOf(::ProductsRepositoryImp) bind ProductsRepository::class

    factoryOf(::GetProductsUseCaseImpl) bind GetProductsUseCase::class

    factoryOf(::GetProductByNameUseCaseImpl) bind GetProductByNameUseCase::class

    singleOf(::ProductsViewModel)

